import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Box,
  Paper,
  Divider,
  Alert,
  CircularProgress
} from '@mui/material';
import {
  Videocam as VideocamIcon,
  Mic as MicIcon,
  Settings as SettingsIcon
} from '@mui/icons-material';

const SettingsDialog = ({ open, onClose }) => {
  const [videoDevices, setVideoDevices] = useState([]);
  const [audioDevices, setAudioDevices] = useState([]);
  const [selectedVideoDevice, setSelectedVideoDevice] = useState('');
  const [selectedAudioDevice, setSelectedAudioDevice] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [testStream, setTestStream] = useState(null);

  useEffect(() => {
    if (open) {
      loadDevices();
      loadSavedSettings();
    }
    
    return () => {
      // Cleanup test stream when dialog closes
      if (testStream) {
        testStream.getTracks().forEach(track => track.stop());
        setTestStream(null);
      }
    };
  }, [open, testStream]);

  const loadDevices = async () => {
    try {
      setLoading(true);
      
      // Request permissions first
      const stream = await navigator.mediaDevices.getUserMedia({ 
        video: true, 
        audio: true 
      });
      
      // Stop the stream immediately - we just needed permission
      stream.getTracks().forEach(track => track.stop());
      
      // Now enumerate devices
      const devices = await navigator.mediaDevices.enumerateDevices();
      
      const videoInputs = devices.filter(device => device.kind === 'videoinput');
      const audioInputs = devices.filter(device => device.kind === 'audioinput');
      
      setVideoDevices(videoInputs);
      setAudioDevices(audioInputs);
      
    } catch (err) {
      console.error('Error loading devices:', err);
      setError('Failed to load media devices. Please check your permissions.');
    } finally {
      setLoading(false);
    }
  };

  const loadSavedSettings = () => {
    const savedVideoDevice = localStorage.getItem('selectedVideoDevice');
    const savedAudioDevice = localStorage.getItem('selectedAudioDevice');
    
    if (savedVideoDevice) {
      setSelectedVideoDevice(savedVideoDevice);
    }
    if (savedAudioDevice) {
      setSelectedAudioDevice(savedAudioDevice);
    }
  };

  const saveSettings = () => {
    localStorage.setItem('selectedVideoDevice', selectedVideoDevice);
    localStorage.setItem('selectedAudioDevice', selectedAudioDevice);
    
    // Dispatch custom event to notify other components of the change
    window.dispatchEvent(new CustomEvent('mediaDevicesChanged', {
      detail: {
        videoDeviceId: selectedVideoDevice,
        audioDeviceId: selectedAudioDevice
      }
    }));
    
    onClose();
  };

  const testVideoDevice = async (deviceId) => {
    try {
      // Stop any existing test stream
      if (testStream) {
        testStream.getTracks().forEach(track => track.stop());
      }
      
      const constraints = {
        video: deviceId ? { deviceId: { exact: deviceId } } : true,
        audio: false
      };
      
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      setTestStream(stream);
      
      // Display the stream in a video element
      const videoElement = document.getElementById('test-video');
      if (videoElement) {
        videoElement.srcObject = stream;
      }
      
    } catch (err) {
      console.error('Error testing video device:', err);
      setError('Failed to test video device');
    }
  };

  const testAudioDevice = async (deviceId) => {
    try {
      const constraints = {
        video: false,
        audio: deviceId ? { deviceId: { exact: deviceId } } : true
      };
      
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      
      // Test audio by showing volume levels
      const audioContext = new AudioContext();
      const analyser = audioContext.createAnalyser();
      const microphone = audioContext.createMediaStreamSource(stream);
      microphone.connect(analyser);
      
      const dataArray = new Uint8Array(analyser.frequencyBinCount);
      
      const checkVolume = () => {
        analyser.getByteFrequencyData(dataArray);
        const average = dataArray.reduce((a, b) => a + b) / dataArray.length;
        
        // Update volume indicator
        const volumeIndicator = document.getElementById('volume-indicator');
        if (volumeIndicator) {
          volumeIndicator.style.width = `${(average / 255) * 100}%`;
        }
        
        if (audioContext.state !== 'closed') {
          requestAnimationFrame(checkVolume);
        }
      };
      
      checkVolume();
      
      // Cleanup after 3 seconds
      setTimeout(() => {
        stream.getTracks().forEach(track => track.stop());
        audioContext.close();
      }, 3000);
      
    } catch (err) {
      console.error('Error testing audio device:', err);
      setError('Failed to test audio device');
    }
  };

  return (
    <Dialog 
      open={open} 
      onClose={onClose} 
      maxWidth="md" 
      fullWidth
      PaperProps={{
        sx: { minHeight: '500px' }
      }}
    >
      <DialogTitle sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
        <SettingsIcon />
        Media Device Settings
      </DialogTitle>
      
      <DialogContent>
        {error && (
          <Alert severity="error" onClose={() => setError('')} sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}
        
        {loading ? (
          <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
            <CircularProgress />
            <Typography sx={{ ml: 2 }}>Loading devices...</Typography>
          </Box>
        ) : (
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
            
            {/* Video Device Selection */}
            <Paper sx={{ p: 3 }}>
              <Typography variant="h6" sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                <VideocamIcon />
                Camera Settings
              </Typography>
              
              <FormControl fullWidth sx={{ mb: 2 }}>
                <InputLabel>Select Camera</InputLabel>
                <Select
                  value={selectedVideoDevice}
                  onChange={(e) => setSelectedVideoDevice(e.target.value)}
                  label="Select Camera"
                >
                  <MenuItem value="">
                    <em>Default Camera</em>
                  </MenuItem>
                  {videoDevices.map((device) => (
                    <MenuItem key={device.deviceId} value={device.deviceId}>
                      {device.label || `Camera ${device.deviceId.slice(0, 8)}...`}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              
              <Button 
                variant="outlined" 
                onClick={() => testVideoDevice(selectedVideoDevice)}
                sx={{ mb: 2 }}
              >
                Test Camera
              </Button>
              
              {/* Video Preview */}
              <Box sx={{ display: 'flex', justifyContent: 'center' }}>
                <video
                  id="test-video"
                  autoPlay
                  muted
                  style={{
                    width: '300px',
                    height: '200px',
                    backgroundColor: '#000',
                    borderRadius: '8px'
                  }}
                />
              </Box>
            </Paper>

            <Divider />

            {/* Audio Device Selection */}
            <Paper sx={{ p: 3 }}>
              <Typography variant="h6" sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                <MicIcon />
                Microphone Settings
              </Typography>
              
              <FormControl fullWidth sx={{ mb: 2 }}>
                <InputLabel>Select Microphone</InputLabel>
                <Select
                  value={selectedAudioDevice}
                  onChange={(e) => setSelectedAudioDevice(e.target.value)}
                  label="Select Microphone"
                >
                  <MenuItem value="">
                    <em>Default Microphone</em>
                  </MenuItem>
                  {audioDevices.map((device) => (
                    <MenuItem key={device.deviceId} value={device.deviceId}>
                      {device.label || `Microphone ${device.deviceId.slice(0, 8)}...`}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              
              <Button 
                variant="outlined" 
                onClick={() => testAudioDevice(selectedAudioDevice)}
                sx={{ mb: 2 }}
              >
                Test Microphone
              </Button>
              
              {/* Volume Level Indicator */}
              <Box sx={{ mb: 2 }}>
                <Typography variant="body2" sx={{ mb: 1 }}>
                  Microphone Level:
                </Typography>
                <Box 
                  sx={{ 
                    width: '100%', 
                    height: '10px', 
                    backgroundColor: '#e0e0e0', 
                    borderRadius: '5px',
                    overflow: 'hidden'
                  }}
                >
                  <Box
                    id="volume-indicator"
                    sx={{
                      height: '100%',
                      backgroundColor: '#4caf50',
                      width: '0%',
                      transition: 'width 0.1s ease'
                    }}
                  />
                </Box>
                <Typography variant="caption" color="text.secondary">
                  Speak to test your microphone level
                </Typography>
              </Box>
            </Paper>

            {/* Additional Settings */}
            <Paper sx={{ p: 3 }}>
              <Typography variant="h6" sx={{ mb: 2 }}>
                Video Quality Settings
              </Typography>
              <Typography variant="body2" color="text.secondary">
                ConvoFlow supports up to 4K (3840×2160) video calling at 60fps. 
                The actual resolution will adapt based on your network conditions and device capabilities.
              </Typography>
            </Paper>
            
          </Box>
        )}
      </DialogContent>
      
      <DialogActions>
        <Button onClick={onClose}>
          Cancel
        </Button>
        <Button onClick={saveSettings} variant="contained">
          Save Settings
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default SettingsDialog;
