import CryptoJS from 'crypto-js';

// Generate a unique encryption key for each user session
export const generateEncryptionKey = () => {
  return CryptoJS.lib.WordArray.random(256/8).toString();
};

// Store encryption key securely in sessionStorage
export const setEncryptionKey = (key) => {
  sessionStorage.setItem('chatEncryptionKey', key);
};

// Retrieve encryption key from sessionStorage
export const getEncryptionKey = () => {
  return sessionStorage.getItem('chatEncryptionKey');
};

// Generate a room-specific encryption key (derived from room ID and user key)
export const generateRoomKey = (roomId, userKey) => {
  return CryptoJS.PBKDF2(userKey + roomId, 'chat-app-salt', {
    keySize: 256/32,
    iterations: 1000
  }).toString();
};

// Encrypt a message using AES-256
export const encryptMessage = (message, roomId) => {
  try {
    const userKey = getEncryptionKey();
    if (!userKey) {
      throw new Error('No encryption key found');
    }
    
    const roomKey = generateRoomKey(roomId, userKey);
    const encrypted = CryptoJS.AES.encrypt(message, roomKey).toString();
    
    return encrypted;
  } catch (error) {
    console.error('Encryption error:', error);
    throw error;
  }
};

// Decrypt a message using AES-256
export const decryptMessage = (encryptedMessage, roomId) => {
  try {
    const userKey = getEncryptionKey();
    if (!userKey) {
      throw new Error('No encryption key found');
    }
    
    const roomKey = generateRoomKey(roomId, userKey);
    const decrypted = CryptoJS.AES.decrypt(encryptedMessage, roomKey);
    const plaintext = decrypted.toString(CryptoJS.enc.Utf8);
    
    if (!plaintext) {
      throw new Error('Failed to decrypt message');
    }
    
    return plaintext;
  } catch (error) {
    console.error('Decryption error:', error);
    return '[Encrypted Message - Unable to decrypt]';
  }
};

// Clear encryption key on logout
export const clearEncryptionKey = () => {
  sessionStorage.removeItem('chatEncryptionKey');
};

// Generate a secure password hash (for client-side validation)
export const hashPassword = (password) => {
  return CryptoJS.SHA256(password).toString();
};

// Validate message integrity
export const validateMessage = (message) => {
  return message && typeof message === 'string' && message.trim().length > 0;
};
