import React, { useState, useRef, useEffect } from 'react';
import {
  Box,
  IconButton,
  Paper,
  Typography,
  Grid,
  Avatar,
  Tooltip,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Badge,
  Menu,
  MenuItem,
  Chip,
  Stack
} from '@mui/material';
import {
  VideoCall as VideoCallIcon,
  CallEnd as CallEndIcon,
  Mic as MicIcon,
  MicOff as MicOffIcon,
  Videocam as VideocamIcon,
  VideocamOff as VideocamOffIcon,
  ScreenShare as ScreenShareIcon,
  StopScreenShare as StopScreenShareIcon,
  Fullscreen as FullscreenIcon,
  FullscreenExit as FullscreenExitIcon,
  VolumeUp as VolumeUpIcon,
  VolumeOff as VolumeOffIcon,
  Settings as SettingsIcon,
  HighQuality as HighQualityIcon,
  NetworkWifi as NetworkWifiIcon
} from '@mui/icons-material';

const VideoCallComponent = ({ 
  webrtcService, 
  currentRoom, 
  currentUser, 
  onCallEnd 
}) => {
  const [isInCall, setIsInCall] = useState(false);
  const [isCallStarted, setIsCallStarted] = useState(false);
  const [isMuted, setIsMuted] = useState(false);
  const [isVideoOff, setIsVideoOff] = useState(false);
  const [isScreenSharing, setIsScreenSharing] = useState(false);
  const [remoteUsers, setRemoteUsers] = useState(new Map());
  const [incomingCall, setIncomingCall] = useState(null);
  const [isFullscreen, setIsFullscreen] = useState(false);
  const [audioEnabled, setAudioEnabled] = useState(true);
  const [currentQuality, setCurrentQuality] = useState('720p');
  const [networkStats, setNetworkStats] = useState({});
  const [showQualityMenu, setShowQualityMenu] = useState(false);
  const [availableQualities, setAvailableQualities] = useState([]);
  const [qualityMenuAnchor, setQualityMenuAnchor] = useState(null);

  const localVideoRef = useRef(null);
  const remoteVideosRef = useRef(new Map());
  const callContainerRef = useRef(null);

  useEffect(() => {
    if (!webrtcService) return;

    // Set up WebRTC service event handlers
    webrtcService.onLocalStreamReady = (stream) => {
      if (localVideoRef.current) {
        localVideoRef.current.srcObject = stream;
      }
    };

    webrtcService.onRemoteStreamAdded = (userId, stream) => {
      setRemoteUsers(prev => {
        const newUsers = new Map(prev);
        newUsers.set(userId, { stream, username: `User ${userId}` });
        return newUsers;
      });

      // Set video element source
      const videoElement = remoteVideosRef.current.get(userId);
      if (videoElement) {
        videoElement.srcObject = stream;
      }
    };

    webrtcService.onRemoteStreamRemoved = (userId) => {
      setRemoteUsers(prev => {
        const newUsers = new Map(prev);
        newUsers.delete(userId);
        return newUsers;
      });
      remoteVideosRef.current.delete(userId);
    };

    webrtcService.onCallStarted = (data) => {
      setIncomingCall(data);
    };

    webrtcService.onCallEnded = () => {
      endCall();
    };

    webrtcService.onUserJoined = (data) => {
      console.log(`User ${data.username} joined the call`);
    };

    webrtcService.onUserLeft = (data) => {
      console.log(`User ${data.username} left the call`);
    };

    webrtcService.onScreenShareStarted = (data) => {
      if (data.fromUserId !== 'local') {
        // Handle remote screen share
        console.log(`${data.fromUsername} started screen sharing`);
      }
    };

    webrtcService.onScreenShareStopped = (data) => {
      if (data.fromUserId !== 'local') {
        // Handle remote screen share stop
        console.log(`${data.fromUsername} stopped screen sharing`);
      }
    };

    // Set current room for WebRTC service
    if (currentRoom) {
      webrtcService.setCurrentRoom(currentRoom.id);
    }

    // Setup quality monitoring
    webrtcService.onBandwidthChange((stats) => {
      setNetworkStats(stats);
    });

    webrtcService.onQualityChange((quality) => {
      setCurrentQuality(quality);
    });

    // Get available quality options
    const qualities = webrtcService.getAvailableQualities();
    setAvailableQualities(qualities);

    // Set initial quality
    const qualityInfo = webrtcService.getCurrentQualityInfo();
    setCurrentQuality(qualityInfo.quality);

    return () => {
      // Cleanup
      if (isInCall) {
        webrtcService.endCall();
      }
    };
  }, [webrtcService, currentRoom]);

  const startVideoCall = async () => {
    try {
      setIsInCall(true);
      setIsCallStarted(true);
      const stream = await webrtcService.startVideoCall(currentRoom.id, true);
      
      if (localVideoRef.current) {
        localVideoRef.current.srcObject = stream;
      }
    } catch (error) {
      console.error('Error starting video call:', error);
      setIsInCall(false);
      setIsCallStarted(false);
      alert('Failed to start video call. Please check your camera and microphone permissions.');
    }
  };

  const startAudioCall = async () => {
    try {
      setIsInCall(true);
      setIsCallStarted(true);
      setIsVideoOff(true);
      const stream = await webrtcService.startVideoCall(currentRoom.id, false);
      
      if (localVideoRef.current) {
        localVideoRef.current.srcObject = stream;
      }
    } catch (error) {
      console.error('Error starting audio call:', error);
      setIsInCall(false);
      setIsCallStarted(false);
      alert('Failed to start audio call. Please check your microphone permissions.');
    }
  };

  const joinCall = async () => {
    try {
      setIsInCall(true);
      const stream = await webrtcService.joinVideoCall(currentRoom.id);
      
      if (localVideoRef.current) {
        localVideoRef.current.srcObject = stream;
      }
      
      setIncomingCall(null);
    } catch (error) {
      console.error('Error joining call:', error);
      setIsInCall(false);
      alert('Failed to join call. Please check your camera and microphone permissions.');
    }
  };

  const endCall = () => {
    webrtcService.endCall();
    setIsInCall(false);
    setIsCallStarted(false);
    setIsMuted(false);
    setIsVideoOff(false);
    setIsScreenSharing(false);
    setRemoteUsers(new Map());
    setIncomingCall(null);
    
    if (localVideoRef.current) {
      localVideoRef.current.srcObject = null;
    }
    
    remoteVideosRef.current.clear();
    
    if (onCallEnd) {
      onCallEnd();
    }
  };

  const leaveCall = () => {
    if (currentRoom) {
      webrtcService.leaveCall(currentRoom.id);
    }
    endCall();
  };

  const toggleMute = () => {
    const muted = webrtcService.toggleMute();
    setIsMuted(muted);
  };

  const toggleVideo = () => {
    const videoOff = webrtcService.toggleVideo();
    setIsVideoOff(videoOff);
  };

  const toggleScreenShare = async () => {
    try {
      if (isScreenSharing) {
        await webrtcService.stopScreenShare(currentRoom.id);
        setIsScreenSharing(false);
      } else {
        await webrtcService.startScreenShare(currentRoom.id);
        setIsScreenSharing(true);
      }
    } catch (error) {
      console.error('Error toggling screen share:', error);
      alert('Failed to share screen. Please try again.');
    }
  };

  const toggleFullscreen = () => {
    if (!isFullscreen) {
      if (callContainerRef.current && callContainerRef.current.requestFullscreen) {
        callContainerRef.current.requestFullscreen();
        setIsFullscreen(true);
      }
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen();
        setIsFullscreen(false);
      }
    }
  };

  const toggleAudio = () => {
    setAudioEnabled(!audioEnabled);
    // Mute/unmute all remote video elements
    remoteVideosRef.current.forEach((videoElement) => {
      videoElement.muted = audioEnabled;
    });
  };

  const declineCall = () => {
    setIncomingCall(null);
  };

  // Quality control methods
  const handleQualityChange = async (quality) => {
    try {
      await webrtcService.setVideoQuality(quality);
      setCurrentQuality(quality);
      setShowQualityMenu(false);
    } catch (error) {
      console.error('Error changing video quality:', error);
      alert('Failed to change video quality. Please try again.');
    }
  };

  const toggleQualityMenu = (event) => {
    if (showQualityMenu) {
      setQualityMenuAnchor(null);
      setShowQualityMenu(false);
    } else {
      setQualityMenuAnchor(event.currentTarget);
      setShowQualityMenu(true);
    }
  };

  const getQualityLabel = (quality) => {
    const labels = {
      '4K': '4K (3840×2160)',
      '1440p': '1440p (2560×1440)',
      '1080p': '1080p (1920×1080)',
      '720p': '720p (1280×720)',
      '480p': '480p (854×480)'
    };
    return labels[quality] || quality;
  };

  const getNetworkQualityIndicator = () => {
    if (!networkStats.receivedBitrate) return { color: 'gray', label: 'Unknown' };
    
    const bitrate = networkStats.receivedBitrate;
    if (bitrate > 15000) return { color: 'green', label: 'Excellent' };
    if (bitrate > 8000) return { color: 'lightgreen', label: 'Good' };
    if (bitrate > 3000) return { color: 'yellow', label: 'Fair' };
    if (bitrate > 1000) return { color: 'orange', label: 'Poor' };
    return { color: 'red', label: 'Very Poor' };
  };

  // Create video element for remote user
  const createRemoteVideoElement = (userId) => {
    if (!remoteVideosRef.current.has(userId)) {
      const video = document.createElement('video');
      video.autoplay = true;
      video.playsInline = true;
      video.muted = !audioEnabled;
      remoteVideosRef.current.set(userId, video);
    }
    return remoteVideosRef.current.get(userId);
  };

  if (!isInCall && !incomingCall) {
    return (
      <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
        <Tooltip title="Start Video Call">
          <IconButton 
            onClick={startVideoCall}
            sx={{ 
              bgcolor: 'primary.main', 
              color: 'white',
              '&:hover': { bgcolor: 'primary.dark' }
            }}
          >
            <VideoCallIcon />
          </IconButton>
        </Tooltip>
        
        <Tooltip title="Start Audio Call">
          <IconButton 
            onClick={startAudioCall}
            sx={{ 
              bgcolor: 'success.main', 
              color: 'white',
              '&:hover': { bgcolor: 'success.dark' }
            }}
          >
            <MicIcon />
          </IconButton>
        </Tooltip>
      </Box>
    );
  }

  return (
    <>
      {/* Incoming Call Dialog */}
      <Dialog open={!!incomingCall} onClose={declineCall}>
        <DialogTitle>Incoming Call</DialogTitle>
        <DialogContent>
          <Typography variant="body1">
            {incomingCall?.fromUsername} is calling you
          </Typography>
          <Typography variant="body2" color="text.secondary">
            Call type: {incomingCall?.callType}
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={declineCall} color="error">
            Decline
          </Button>
          <Button onClick={joinCall} color="success" variant="contained">
            Join Call
          </Button>
        </DialogActions>
      </Dialog>

      {/* Video Call Interface */}
      <Paper 
        ref={callContainerRef}
        sx={{ 
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          zIndex: 1300,
          bgcolor: 'black',
          display: 'flex',
          flexDirection: 'column'
        }}
      >
        {/* Video Grid */}
        <Box sx={{ flex: 1, position: 'relative', overflow: 'hidden' }}>
          <Grid container sx={{ height: '100%' }}>
            {/* Local Video */}
            <Grid item xs={remoteUsers.size > 0 ? 6 : 12} sx={{ position: 'relative' }}>
              <video
                ref={localVideoRef}
                autoPlay
                playsInline
                muted
                style={{
                  width: '100%',
                  height: '100%',
                  objectFit: 'cover',
                  transform: 'scaleX(-1)' // Mirror local video
                }}
              />
              
              {/* Local User Info */}
              <Box
                sx={{
                  position: 'absolute',
                  bottom: 16,
                  left: 16,
                  bgcolor: 'rgba(0,0,0,0.7)',
                  color: 'white',
                  px: 2,
                  py: 1,
                  borderRadius: 1,
                  display: 'flex',
                  alignItems: 'center',
                  gap: 1
                }}
              >
                <Avatar sx={{ width: 24, height: 24 }}>
                  {currentUser?.username?.[0]?.toUpperCase()}
                </Avatar>
                <Typography variant="body2">
                  You {isMuted && '(Muted)'} {isVideoOff && '(No Video)'}
                </Typography>
              </Box>

              {/* Screen Share Indicator */}
              {isScreenSharing && (
                <Box
                  sx={{
                    position: 'absolute',
                    top: 16,
                    left: 16,
                    bgcolor: 'rgba(255,0,0,0.8)',
                    color: 'white',
                    px: 2,
                    py: 1,
                    borderRadius: 1,
                    display: 'flex',
                    alignItems: 'center',
                    gap: 1
                  }}
                >
                  <ScreenShareIcon />
                  <Typography variant="body2">Screen Sharing</Typography>
                </Box>
              )}
            </Grid>

            {/* Remote Videos */}
            {Array.from(remoteUsers.entries()).map(([userId, userInfo]) => (
              <Grid key={userId} item xs={6} sx={{ position: 'relative' }}>
                <video
                  ref={(el) => {
                    if (el && userInfo.stream) {
                      el.srcObject = userInfo.stream;
                      remoteVideosRef.current.set(userId, el);
                    }
                  }}
                  autoPlay
                  playsInline
                  muted={!audioEnabled}
                  style={{
                    width: '100%',
                    height: '100%',
                    objectFit: 'cover'
                  }}
                />
                
                {/* Remote User Info */}
                <Box
                  sx={{
                    position: 'absolute',
                    bottom: 16,
                    left: 16,
                    bgcolor: 'rgba(0,0,0,0.7)',
                    color: 'white',
                    px: 2,
                    py: 1,
                    borderRadius: 1,
                    display: 'flex',
                    alignItems: 'center',
                    gap: 1
                  }}
                >
                  <Avatar sx={{ width: 24, height: 24 }}>
                    {userInfo.username?.[0]?.toUpperCase()}
                  </Avatar>
                  <Typography variant="body2">
                    {userInfo.username}
                  </Typography>
                </Box>
              </Grid>
            ))}
          </Grid>
        </Box>

        {/* Call Controls */}
        <Box
          sx={{
            position: 'absolute',
            bottom: 24,
            left: '50%',
            transform: 'translateX(-50%)',
            display: 'flex',
            gap: 2,
            bgcolor: 'rgba(0,0,0,0.8)',
            borderRadius: 4,
            p: 2
          }}
        >
          {/* Mute/Unmute */}
          <Tooltip title={isMuted ? 'Unmute' : 'Mute'}>
            <IconButton
              onClick={toggleMute}
              sx={{
                bgcolor: isMuted ? 'error.main' : 'grey.700',
                color: 'white',
                '&:hover': { 
                  bgcolor: isMuted ? 'error.dark' : 'grey.600' 
                }
              }}
            >
              {isMuted ? <MicOffIcon /> : <MicIcon />}
            </IconButton>
          </Tooltip>

          {/* Video On/Off */}
          <Tooltip title={isVideoOff ? 'Turn Video On' : 'Turn Video Off'}>
            <IconButton
              onClick={toggleVideo}
              sx={{
                bgcolor: isVideoOff ? 'error.main' : 'grey.700',
                color: 'white',
                '&:hover': { 
                  bgcolor: isVideoOff ? 'error.dark' : 'grey.600' 
                }
              }}
            >
              {isVideoOff ? <VideocamOffIcon /> : <VideocamIcon />}
            </IconButton>
          </Tooltip>

          {/* Screen Share */}
          <Tooltip title={isScreenSharing ? 'Stop Screen Share' : 'Share Screen'}>
            <IconButton
              onClick={toggleScreenShare}
              sx={{
                bgcolor: isScreenSharing ? 'warning.main' : 'grey.700',
                color: 'white',
                '&:hover': { 
                  bgcolor: isScreenSharing ? 'warning.dark' : 'grey.600' 
                }
              }}
            >
              {isScreenSharing ? <StopScreenShareIcon /> : <ScreenShareIcon />}
            </IconButton>
          </Tooltip>

          {/* Audio Toggle */}
          <Tooltip title={audioEnabled ? 'Mute Audio' : 'Unmute Audio'}>
            <IconButton
              onClick={toggleAudio}
              sx={{
                bgcolor: audioEnabled ? 'grey.700' : 'error.main',
                color: 'white',
                '&:hover': { 
                  bgcolor: audioEnabled ? 'grey.600' : 'error.dark' 
                }
              }}
            >
              {audioEnabled ? <VolumeUpIcon /> : <VolumeOffIcon />}
            </IconButton>
          </Tooltip>

          {/* Fullscreen */}
          <Tooltip title={isFullscreen ? 'Exit Fullscreen' : 'Fullscreen'}>
            <IconButton
              onClick={toggleFullscreen}
              sx={{
                bgcolor: 'grey.700',
                color: 'white',
                '&:hover': { bgcolor: 'grey.600' }
              }}
            >
              {isFullscreen ? <FullscreenExitIcon /> : <FullscreenIcon />}
            </IconButton>
          </Tooltip>

          {/* Quality Settings */}
          <Tooltip title="Video Quality Settings">
            <IconButton
              onClick={toggleQualityMenu}
              sx={{
                bgcolor: 'grey.700',
                color: 'white',
                '&:hover': { bgcolor: 'grey.600' }
              }}
            >
              <SettingsIcon />
            </IconButton>
          </Tooltip>

          {/* End Call */}
          <Tooltip title="End Call">
            <IconButton
              onClick={leaveCall}
              sx={{
                bgcolor: 'error.main',
                color: 'white',
                '&:hover': { bgcolor: 'error.dark' }
              }}
            >
              <CallEndIcon />
            </IconButton>
          </Tooltip>
        </Box>

        {/* Network Status & Quality Info */}
        <Box
          sx={{
            position: 'absolute',
            top: 24,
            left: 24,
            bgcolor: 'rgba(0,0,0,0.7)',
            color: 'white',
            px: 2,
            py: 1,
            borderRadius: 1
          }}
        >
          <Stack direction="row" spacing={2} alignItems="center">
            <Box display="flex" alignItems="center" gap={1}>
              <NetworkWifiIcon fontSize="small" />
              <Chip
                label={getNetworkQualityIndicator().label}
                size="small"
                sx={{
                  bgcolor: getNetworkQualityIndicator().color,
                  color: 'white',
                  fontSize: '0.75rem'
                }}
              />
            </Box>
            <Box display="flex" alignItems="center" gap={1}>
              <HighQualityIcon fontSize="small" />
              <Typography variant="body2">
                {getQualityLabel(currentQuality)}
              </Typography>
            </Box>
            {networkStats.receivedBitrate && (
              <Typography variant="caption">
                {Math.round(networkStats.receivedBitrate)} kbps
              </Typography>
            )}
          </Stack>
        </Box>

        {/* Participant Count */}
        <Box
          sx={{
            position: 'absolute',
            top: 24,
            right: 24,
            bgcolor: 'rgba(0,0,0,0.7)',
            color: 'white',
            px: 2,
            py: 1,
            borderRadius: 1
          }}
        >
          <Typography variant="body2">
            {remoteUsers.size + 1} participant{remoteUsers.size !== 0 ? 's' : ''}
          </Typography>
        </Box>
      </Paper>

      {/* Quality Selection Menu */}
      <Menu
        anchorEl={qualityMenuAnchor}
        open={showQualityMenu}
        onClose={() => {
          setShowQualityMenu(false);
          setQualityMenuAnchor(null);
        }}
        PaperProps={{
          sx: {
            bgcolor: 'rgba(0,0,0,0.9)',
            color: 'white',
            minWidth: 250
          }
        }}
      >
        <MenuItem disabled>
          <Typography variant="subtitle2" sx={{ fontWeight: 'bold' }}>
            Video Quality
          </Typography>
        </MenuItem>
        {availableQualities.map((quality) => (
          <MenuItem
            key={quality}
            onClick={() => handleQualityChange(quality)}
            selected={quality === currentQuality}
            sx={{
              '&.Mui-selected': {
                bgcolor: 'primary.main',
                '&:hover': {
                  bgcolor: 'primary.dark'
                }
              }
            }}
          >
            <Stack direction="row" justifyContent="space-between" width="100%">
              <Typography>{getQualityLabel(quality)}</Typography>
              {quality === currentQuality && (
                <Chip
                  label="Current"
                  size="small"
                  color="primary"
                  sx={{ height: 20, fontSize: '0.7rem' }}
                />
              )}
            </Stack>
          </MenuItem>
        ))}
        <MenuItem disabled>
          <Typography variant="caption" sx={{ color: 'grey.400' }}>
            Quality adapts automatically based on network conditions
          </Typography>
        </MenuItem>
      </Menu>
    </>
  );
};

export default VideoCallComponent;
