// Enhanced WebRTC service with encryption for signaling

import CryptoJS from 'crypto-js';

class SecureWebRTCService {
  constructor(socket) {
    this.socket = socket;
    this.localStream = null;
    this.screenStream = null;
    this.peerConnections = new Map();
    this.remoteStreams = new Map();
    this.signalingKey = null; // Will be derived from room encryption key
    
    // Enhanced STUN/TURN configuration with authentication
    this.rtcConfiguration = {
      iceServers: [
        { urls: 'stun:stun.l.google.com:19302' },
        { urls: 'stun:stun1.l.google.com:19302' },
        {
          urls: 'turn:your-turn-server.com:3478',
          username: 'turnuser', 
          credential: 'turnpass'
        },
        {
          urls: 'turns:your-turn-server.com:5349',
          username: 'turnuser', 
          credential: 'turnpass'
        }
      ],
      iceCandidatePoolSize: 10,
      bundlePolicy: 'max-bundle',
      rtcpMuxPolicy: 'require'
    };

    // Video quality presets
    this.videoQualityPresets = {
      '4K': {
        width: { ideal: 3840, max: 3840 },
        height: { ideal: 2160, max: 2160 },
        frameRate: { ideal: 60, min: 30, max: 60 },
        bitrate: 20000000 // 20 Mbps
      },
      '1440p': {
        width: { ideal: 2560, max: 2560 },
        height: { ideal: 1440, max: 1440 },
        frameRate: { ideal: 60, min: 30, max: 60 },
        bitrate: 10000000 // 10 Mbps
      },
      '1080p': {
        width: { ideal: 1920, max: 1920 },
        height: { ideal: 1080, max: 1080 },
        frameRate: { ideal: 60, min: 30, max: 60 },
        bitrate: 6000000 // 6 Mbps
      },
      '720p': {
        width: { ideal: 1280, max: 1280 },
        height: { ideal: 720, max: 720 },
        frameRate: { ideal: 60, min: 30, max: 60 },
        bitrate: 3000000 // 3 Mbps
      },
      '480p': {
        width: { ideal: 854, max: 854 },
        height: { ideal: 480, max: 480 },
        frameRate: { ideal: 30, min: 15, max: 30 },
        bitrate: 1500000 // 1.5 Mbps
      }
    };

    this.currentVideoQuality = '1080p'; // Default quality
    this.adaptiveQuality = true; // Enable adaptive quality    this.setupSocketListeners();
  }

  // Initialize signaling encryption key from room key
  initializeSignalingKey(roomId) {
    const roomKey = localStorage.getItem(`room_key_${roomId}`);
    if (roomKey) {
      // Derive signaling key from room key
      this.signalingKey = CryptoJS.PBKDF2(roomKey, 'webrtc-signaling', {
        keySize: 256/32,
        iterations: 1000
      }).toString();
    }
  }

  // Encrypt signaling message
  encryptSignalingMessage(message) {
    if (!this.signalingKey) return message;
    
    try {
      const messageStr = JSON.stringify(message);
      const encrypted = CryptoJS.AES.encrypt(messageStr, this.signalingKey).toString();
      return { encrypted: true, data: encrypted };
    } catch (error) {
      console.error('Error encrypting signaling message:', error);
      return message;
    }
  }

  // Decrypt signaling message
  decryptSignalingMessage(message) {
    if (!message.encrypted || !this.signalingKey) return message;
    
    try {
      const decrypted = CryptoJS.AES.decrypt(message.data, this.signalingKey);
      const decryptedStr = decrypted.toString(CryptoJS.enc.Utf8);
      return JSON.parse(decryptedStr);
    } catch (error) {
      console.error('Error decrypting signaling message:', error);
      return null;
    }
  }

  setupSocketListeners() {
    // Handle encrypted WebRTC offers
    this.socket.on('webrtc-offer', async (data) => {
      const decryptedData = this.decryptSignalingMessage(data);
      if (!decryptedData) return;
      
      const { offer, fromUserId, fromUsername } = decryptedData;
      console.log(`Received encrypted WebRTC offer from ${fromUsername}`);
      
      try {
        await this.handleOffer(offer, fromUserId);
      } catch (error) {
        console.error('Error handling WebRTC offer:', error);
      }
    });

    // Handle encrypted WebRTC answers
    this.socket.on('webrtc-answer', async (data) => {
      const decryptedData = this.decryptSignalingMessage(data);
      if (!decryptedData) return;
      
      const { answer, fromUserId, fromUsername } = decryptedData;
      console.log(`Received encrypted WebRTC answer from ${fromUsername}`);
      
      try {
        const peerConnection = this.peerConnections.get(fromUserId);
        if (peerConnection) {
          await peerConnection.setRemoteDescription(new RTCSessionDescription(answer));
        }
      } catch (error) {
        console.error('Error handling WebRTC answer:', error);
      }
    });

    // Handle encrypted ICE candidates
    this.socket.on('webrtc-ice-candidate', async (data) => {
      const decryptedData = this.decryptSignalingMessage(data);
      if (!decryptedData) return;
      
      const { candidate, fromUserId } = decryptedData;
      
      try {
        const peerConnection = this.peerConnections.get(fromUserId);
        if (peerConnection && candidate) {
          await peerConnection.addIceCandidate(new RTCIceCandidate(candidate));
        }
      } catch (error) {
        console.error('Error handling ICE candidate:', error);
      }
    });

    // Other event handlers remain the same but could be encrypted too
    this.socket.on('video-call-started', (data) => {
      if (this.onCallStarted) {
        this.onCallStarted(data);
      }
    });

    this.socket.on('video-call-user-joined', (data) => {
      if (this.onUserJoined) {
        this.onUserJoined(data);
      }
    });

    this.socket.on('video-call-user-left', (data) => {
      const { userId } = data;
      this.removePeerConnection(userId);
      if (this.onUserLeft) {
        this.onUserLeft(data);
      }
    });

    this.socket.on('video-call-ended', (data) => {
      this.endCall();
      if (this.onCallEnded) {
        this.onCallEnded(data);
      }
    });

    this.socket.on('screen-share-started', (data) => {
      if (this.onScreenShareStarted) {
        this.onScreenShareStarted(data);
      }
    });

    this.socket.on('screen-share-stopped', (data) => {
      if (this.onScreenShareStopped) {
        this.onScreenShareStopped(data);
      }
    });
  }

  // Enhanced peer connection with additional security
  async createPeerConnection(userId, roomId) {
    const peerConnection = new RTCPeerConnection(this.rtcConfiguration);
    
    // Add local stream tracks
    if (this.localStream) {
      this.localStream.getTracks().forEach(track => {
        peerConnection.addTrack(track, this.localStream);
      });
    }

    // Handle remote stream
    peerConnection.ontrack = (event) => {
      console.log('Received remote stream from user:', userId);
      const [remoteStream] = event.streams;
      this.remoteStreams.set(userId, remoteStream);
      
      if (this.onRemoteStreamAdded) {
        this.onRemoteStreamAdded(userId, remoteStream);
      }
    };

    // Handle encrypted ICE candidates
    peerConnection.onicecandidate = (event) => {
      if (event.candidate) {
        const encryptedCandidate = this.encryptSignalingMessage({
          roomId,
          candidate: event.candidate,
          targetUserId: userId
        });
        
        this.socket.emit('webrtc-ice-candidate', encryptedCandidate);
      }
    };

    // Enhanced connection state monitoring
    peerConnection.onconnectionstatechange = () => {
      console.log(`Peer connection state with ${userId}:`, peerConnection.connectionState);
      
      if (peerConnection.connectionState === 'connected') {
        console.log(`Secure connection established with user ${userId}`);
      } else if (peerConnection.connectionState === 'disconnected' || 
                 peerConnection.connectionState === 'failed') {
        this.removePeerConnection(userId);
      }
    };

    // Monitor ICE connection state
    peerConnection.oniceconnectionstatechange = () => {
      console.log(`ICE connection state with ${userId}:`, peerConnection.iceConnectionState);
    };

    this.peerConnections.set(userId, peerConnection);
    return peerConnection;
  }

  // Monitor network quality and adapt video accordingly
  async monitorNetworkQuality() {
    if (this.peerConnections.size === 0) return;

    try {
      for (const [userId, peerConnection] of this.peerConnections) {
        const stats = await peerConnection.getStats();
        let totalBytesReceived = 0;
        let totalBytesSent = 0;
        let packetsLost = 0;
        let jitter = 0;

        stats.forEach(report => {
          if (report.type === 'inbound-rtp' && report.mediaType === 'video') {
            totalBytesReceived += report.bytesReceived || 0;
            packetsLost += report.packetsLost || 0;
            jitter += report.jitter || 0;
          }
          if (report.type === 'outbound-rtp' && report.mediaType === 'video') {
            totalBytesSent += report.bytesSent || 0;
          }
        });

        // Calculate quality metrics
        const timestamp = Date.now();
        const userStatsKey = `stats_${userId}`;
        
        if (this[userStatsKey]) {
          const timeDiff = (timestamp - this[userStatsKey].timestamp) / 1000;
          const receivedBitrate = ((totalBytesReceived - this[userStatsKey].bytesReceived) * 8) / timeDiff / 1000; // kbps
          const sentBitrate = ((totalBytesSent - this[userStatsKey].bytesSent) * 8) / timeDiff / 1000; // kbps

          // Adapt quality based on available bandwidth
          if (receivedBitrate < 1000 && this.currentVideoQuality !== '480p') {
            await this.adaptVideoQuality('480p');
          } else if (receivedBitrate > 2000 && receivedBitrate < 5000 && this.currentVideoQuality !== '720p') {
            await this.adaptVideoQuality('720p');
          } else if (receivedBitrate > 8000 && receivedBitrate < 15000 && this.currentVideoQuality !== '1080p') {
            await this.adaptVideoQuality('1080p');
          } else if (receivedBitrate > 15000 && this.currentVideoQuality !== '1440p') {
            await this.adaptVideoQuality('1440p');
          } else if (receivedBitrate > 25000 && this.currentVideoQuality !== '4K') {
            await this.adaptVideoQuality('4K');
          }

          if (this.bandwidthChangeCallback) {
            this.bandwidthChangeCallback({
              userId,
              receivedBitrate,
              sentBitrate,
              packetsLost,
              jitter,
              quality: this.currentVideoQuality
            });
          }
        }

        this[userStatsKey] = {
          timestamp,
          bytesReceived: totalBytesReceived,
          bytesSent: totalBytesSent,
          packetsLost,
          jitter
        };
      }
    } catch (error) {
      console.error('Error monitoring network quality:', error);
    }
  }

  // Adapt video quality dynamically
  async adaptVideoQuality(newQuality) {
    if (!this.localStream || this.currentVideoQuality === newQuality) return;

    try {
      const videoTrack = this.localStream.getVideoTracks()[0];
      if (!videoTrack) return;

      const constraints = this.videoQualityPresets[newQuality];
      await videoTrack.applyConstraints(constraints);
      
      this.currentVideoQuality = newQuality;
      console.log(`Video quality adapted to: ${newQuality}`);
      
      if (this.qualityChangeCallback) {
        this.qualityChangeCallback(newQuality);
      }
    } catch (error) {
      console.error('Error adapting video quality:', error);
    }
  }

  // Start network quality monitoring
  startNetworkMonitoring() {
    if (this.networkMonitorInterval) return;
    
    this.networkMonitorInterval = setInterval(() => {
      this.monitorNetworkQuality();
    }, 5000); // Monitor every 5 seconds
  }

  // Stop network quality monitoring
  stopNetworkMonitoring() {
    if (this.networkMonitorInterval) {
      clearInterval(this.networkMonitorInterval);
      this.networkMonitorInterval = null;
    }
  }

  // Add handling for bandwidth and quality changes
  onBandwidthChange(callback) {
    this.bandwidthChangeCallback = callback;
  }

  onQualityChange(callback) {
    this.qualityChangeCallback = callback;
  }

  // Manual quality setting
  async setVideoQuality(quality) {
    if (this.videoQualityPresets[quality]) {
      await this.adaptVideoQuality(quality);
    }
  }

  // Get available quality options
  getAvailableQualities() {
    return Object.keys(this.videoQualityPresets);
  }

  // Get current quality information
  getCurrentQualityInfo() {
    const preset = this.videoQualityPresets[this.currentVideoQuality];
    return {
      quality: this.currentVideoQuality,
      resolution: `${preset.width.ideal}x${preset.height.ideal}`,
      frameRate: preset.frameRate.ideal,
      maxBitrate: this.getMaxBitrateForQuality(this.currentVideoQuality)
    };
  }

  // Get maximum bitrate for quality setting
  getMaxBitrateForQuality(quality) {
    const bitrateMap = {
      '4K': 20000,
      '1440p': 10000,
      '1080p': 5000,
      '720p': 3000,
      '480p': 1500
    };
    return bitrateMap[quality] || 3000;
  }

  // Create encrypted offer
  async createOffer(userId, roomId) {
    const peerConnection = await this.createPeerConnection(userId, roomId);
    
    try {
      const offer = await peerConnection.createOffer({
        offerToReceiveAudio: true,
        offerToReceiveVideo: true
      });
      
      await peerConnection.setLocalDescription(offer);
      
      // Encrypt the offer before sending
      const encryptedOffer = this.encryptSignalingMessage({
        roomId,
        offer,
        targetUserId: userId
      });
      
      this.socket.emit('webrtc-offer', encryptedOffer);
    } catch (error) {
      console.error('Error creating offer:', error);
      throw error;
    }
  }

  // Handle encrypted incoming offer
  async handleOffer(offer, fromUserId) {
    const roomId = this.currentRoomId;
    const peerConnection = await this.createPeerConnection(fromUserId, roomId);
    
    try {
      await peerConnection.setRemoteDescription(new RTCSessionDescription(offer));
      
      const answer = await peerConnection.createAnswer();
      await peerConnection.setLocalDescription(answer);
      
      // Encrypt the answer before sending
      const encryptedAnswer = this.encryptSignalingMessage({
        roomId,
        answer,
        targetUserId: fromUserId
      });
      
      this.socket.emit('webrtc-answer', encryptedAnswer);
    } catch (error) {
      console.error('Error handling offer:', error);
      throw error;
    }
  }

  // Start video call with encryption setup
  async startVideoCall(roomId, includeVideo = true, quality = null) {
    try {
      // Initialize signaling encryption
      this.initializeSignalingKey(roomId);
      
      // Use specified quality or current default
      const videoQuality = quality || this.currentVideoQuality;
      const videoConstraints = this.videoQualityPresets[videoQuality];
      
      // Get user media with high-quality constraints
      this.localStream = await navigator.mediaDevices.getUserMedia({
        video: includeVideo ? {
          ...videoConstraints,
          facingMode: 'user',
          aspectRatio: { ideal: 16/9 },
          resizeMode: 'crop-and-scale'
        } : false,
        audio: {
          echoCancellation: true,
          noiseSuppression: true,
          autoGainControl: true,
          sampleRate: 48000,
          channelCount: 2,
          sampleSize: 16,
          latency: 0.01 // Low latency for real-time communication
        }
      });

      // Notify server about call start
      this.socket.emit('video-call-start', {
        roomId,
        callType: includeVideo ? 'video' : 'audio'
      });

      if (this.onLocalStreamReady) {
        this.onLocalStreamReady(this.localStream);
      }

      // Start network quality monitoring
      this.startNetworkMonitoring();
      
      console.log(`Video call started with encryption enabled at ${videoQuality} quality`);
      return this.localStream;
    } catch (error) {
      console.error('Error starting video call:', error);
      throw error;
    }
  }

  // Enhanced screen sharing with encryption
  async startScreenShare(roomId) {
    try {
      this.screenStream = await navigator.mediaDevices.getDisplayMedia({
        video: {
          cursor: 'always',
          width: { ideal: 1920, max: 3840 },
          height: { ideal: 1080, max: 2160 },
          frameRate: { ideal: 15, max: 30 }
        },
        audio: {
          echoCancellation: false,
          noiseSuppression: false,
          sampleRate: 48000
        }
      });

      // Replace video track in all peer connections
      const videoTrack = this.screenStream.getVideoTracks()[0];
      
      for (const [userId, peerConnection] of this.peerConnections) {
        const sender = peerConnection.getSenders().find(s => 
          s.track && s.track.kind === 'video'
        );
        
        if (sender) {
          await sender.replaceTrack(videoTrack);
        }
      }

      // Handle screen share end
      videoTrack.onended = () => {
        this.stopScreenShare(roomId);
      };

      // Notify server
      this.socket.emit('screen-share-start', { roomId });

      if (this.onScreenShareStarted) {
        this.onScreenShareStarted({ fromUserId: 'local', stream: this.screenStream });
      }

      return this.screenStream;
    } catch (error) {
      console.error('Error starting screen share:', error);
      throw error;
    }
  }

  // Get media devices for device selection
  async getMediaDevices() {
    try {
      const devices = await navigator.mediaDevices.enumerateDevices();
      return {
        videoInputs: devices.filter(device => device.kind === 'videoinput'),
        audioInputs: devices.filter(device => device.kind === 'audioinput'),
        audioOutputs: devices.filter(device => device.kind === 'audiooutput')
      };
    } catch (error) {
      console.error('Error getting media devices:', error);
      return { videoInputs: [], audioInputs: [], audioOutputs: [] };
    }
  }

  // Switch camera device
  async switchCamera(deviceId) {
    try {
      const newStream = await navigator.mediaDevices.getUserMedia({
        video: { deviceId: { exact: deviceId } },
        audio: this.localStream.getAudioTracks()[0]?.getSettings()
      });

      const videoTrack = newStream.getVideoTracks()[0];
      
      // Replace video track in all peer connections
      for (const [userId, peerConnection] of this.peerConnections) {
        const sender = peerConnection.getSenders().find(s => 
          s.track && s.track.kind === 'video'
        );
        
        if (sender) {
          await sender.replaceTrack(videoTrack);
        }
      }

      // Stop old video track
      const oldVideoTrack = this.localStream.getVideoTracks()[0];
      if (oldVideoTrack) {
        oldVideoTrack.stop();
      }

      // Update local stream
      this.localStream.removeTrack(oldVideoTrack);
      this.localStream.addTrack(videoTrack);

      if (this.onLocalStreamReady) {
        this.onLocalStreamReady(this.localStream);
      }
    } catch (error) {
      console.error('Error switching camera:', error);
      throw error;
    }
  }

  // Get connection statistics
  async getConnectionStats() {
    const stats = {};
    
    for (const [userId, peerConnection] of this.peerConnections) {
      try {
        const peerStats = await peerConnection.getStats();
        stats[userId] = this.parseRTCStats(peerStats);
      } catch (error) {
        console.error(`Error getting stats for user ${userId}:`, error);
      }
    }
    
    return stats;
  }

  // Parse RTC statistics
  parseRTCStats(statsReport) {
    const parsedStats = {
      video: { inbound: {}, outbound: {} },
      audio: { inbound: {}, outbound: {} },
      connection: {}
    };

    statsReport.forEach((stat) => {
      if (stat.type === 'inbound-rtp') {
        if (stat.kind === 'video') {
          parsedStats.video.inbound = {
            packetsReceived: stat.packetsReceived,
            bytesReceived: stat.bytesReceived,
            packetsLost: stat.packetsLost,
            frameWidth: stat.frameWidth,
            frameHeight: stat.frameHeight,
            framesPerSecond: stat.framesPerSecond
          };
        } else if (stat.kind === 'audio') {
          parsedStats.audio.inbound = {
            packetsReceived: stat.packetsReceived,
            bytesReceived: stat.bytesReceived,
            packetsLost: stat.packetsLost,
            audioLevel: stat.audioLevel
          };
        }
      } else if (stat.type === 'outbound-rtp') {
        if (stat.kind === 'video') {
          parsedStats.video.outbound = {
            packetsSent: stat.packetsSent,
            bytesSent: stat.bytesSent,
            frameWidth: stat.frameWidth,
            frameHeight: stat.frameHeight,
            framesPerSecond: stat.framesPerSecond
          };
        } else if (stat.kind === 'audio') {
          parsedStats.audio.outbound = {
            packetsSent: stat.packetsSent,
            bytesSent: stat.bytesSent
          };
        }
      } else if (stat.type === 'candidate-pair' && stat.state === 'succeeded') {
        parsedStats.connection = {
          localCandidateType: stat.localCandidateType,
          remoteCandidateType: stat.remoteCandidateType,
          currentRoundTripTime: stat.currentRoundTripTime * 1000, // Convert to ms
          availableOutgoingBitrate: stat.availableOutgoingBitrate
        };
      }
    });

    return parsedStats;
  }

  // Set current room and initialize encryption
  setCurrentRoom(roomId) {
    this.currentRoomId = roomId;
    this.initializeSignalingKey(roomId);
  }

  // Call management methods
  endCall() {
    console.log('Ending video call');
    
    // Stop network monitoring
    this.stopNetworkMonitoring();
    
    // Stop local stream
    if (this.localStream) {
      this.localStream.getTracks().forEach(track => track.stop());
      this.localStream = null;
    }
    
    // Stop screen stream
    if (this.screenStream) {
      this.screenStream.getTracks().forEach(track => track.stop());
      this.screenStream = null;
    }
    
    // Close all peer connections
    this.peerConnections.forEach((pc, userId) => {
      pc.close();
      this.peerConnections.delete(userId);
    });
    
    // Clear remote streams
    this.remoteStreams.clear();
    
    // Notify server
    if (this.currentRoomId) {
      this.socket.emit('video-call-end', { roomId: this.currentRoomId });
    }
    
    // Clear encryption key
    this.signalingKey = null;
    this.currentRoomId = null;
    
    if (this.onCallEnded) {
      this.onCallEnded();
    }
  }

  // Toggle audio mute
  toggleMute() {
    if (!this.localStream) return false;
    
    const audioTrack = this.localStream.getAudioTracks()[0];
    if (audioTrack) {
      audioTrack.enabled = !audioTrack.enabled;
      console.log('Audio muted:', !audioTrack.enabled);
      return !audioTrack.enabled;
    }
    return false;
  }

  // Toggle video
  toggleVideo() {
    if (!this.localStream) return false;
    
    const videoTrack = this.localStream.getVideoTracks()[0];
    if (videoTrack) {
      videoTrack.enabled = !videoTrack.enabled;
      console.log('Video disabled:', !videoTrack.enabled);
      return !videoTrack.enabled;
    }
    return false;
  }

  // Get call state
  getCallState() {
    return {
      hasLocalStream: !!this.localStream,
      hasScreenStream: !!this.screenStream,
      connectedPeers: this.peerConnections.size,
      currentQuality: this.currentVideoQuality,
      isMonitoring: !!this.networkMonitorInterval
    };
  }

  // Event handlers
  onLocalStreamReady = null;
  onRemoteStreamAdded = null;
  onRemoteStreamRemoved = null;
  onCallStarted = null;
  onCallEnded = null;
  onUserJoined = null;
  onUserLeft = null;
  onScreenShareStarted = null;
  onScreenShareStopped = null;
  onConnectionStatsUpdate = null;
}

export default SecureWebRTCService;
