import React, { useState, useEffect } from 'react';
import {
  Box,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Typography,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Alert,
  Autocomplete,
  CircularProgress
} from '@mui/material';
import {
  Add as AddIcon,
  Person as PersonIcon,
  Message as MessageIcon
} from '@mui/icons-material';
import { conversationsAPI, usersAPI } from '../utils/api';
import { decryptMessage } from '../utils/encryption';

const ConversationsList = ({ onConversationSelect, selectedConversation }) => {
  const [conversations, setConversations] = useState([]);
  const [createConversationDialog, setCreateConversationDialog] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [loading, setLoading] = useState(false);
  const [searchLoading, setSearchLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  useEffect(() => {
    loadConversations();
  }, []);

  const loadConversations = async () => {
    try {
      setLoading(true);
      const response = await conversationsAPI.getConversations();
      setConversations(response.data);
    } catch (error) {
      setError('Failed to load conversations');
    } finally {
      setLoading(false);
    }
  };

  const searchUsers = async (query) => {
    if (!query || query.length < 2) {
      setSearchResults([]);
      return;
    }

    try {
      setSearchLoading(true);
      const response = await usersAPI.searchUsers(query);
      setSearchResults(response.data);
    } catch (error) {
      console.error('Failed to search users:', error);
    } finally {
      setSearchLoading(false);
    }
  };

  const createConversation = async () => {
    if (!selectedUser) return;

    try {
      const response = await conversationsAPI.createConversation(selectedUser.id);
      setCreateConversationDialog(false);
      setSelectedUser(null);
      setSearchQuery('');
      setSearchResults([]);
      setSuccess('Conversation created successfully!');
      loadConversations(); // Reload conversations
    } catch (error) {
      setError(error.response?.data?.error || 'Failed to create conversation');
    }
  };

  const formatLastMessageTime = (timestamp) => {
    if (!timestamp) return '';
    
    const date = new Date(timestamp);
    const now = new Date();
    const diff = now - date;
    
    if (diff < 60000) return 'now';
    if (diff < 3600000) return `${Math.floor(diff / 60000)}m ago`;
    if (diff < 86400000) return `${Math.floor(diff / 3600000)}h ago`;
    return date.toLocaleDateString();
  };

  const getLastMessagePreview = (conversation) => {
    if (!conversation.last_message) return 'No messages yet';
    
    try {
      // For preview, we'll show encrypted indicator since we can't decrypt without full context
      return '[Encrypted message]';
    } catch (error) {
      return '[Encrypted message]';
    }
  };

  return (
    <Box sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      {error && (
        <Alert severity="error" onClose={() => setError('')} sx={{ m: 1 }}>
          {error}
        </Alert>
      )}
      
      {success && (
        <Alert severity="success" onClose={() => setSuccess('')} sx={{ m: 1 }}>
          {success}
        </Alert>
      )}

      {/* Header */}
      <Box sx={{ p: 2, borderBottom: 1, borderColor: 'divider' }}>
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
          <Typography variant="h6">
            Direct Messages
          </Typography>
          <IconButton 
            size="small" 
            onClick={() => setCreateConversationDialog(true)}
            color="primary"
          >
            <AddIcon />
          </IconButton>
        </Box>
      </Box>

      {/* Conversations List */}
      <Box sx={{ flexGrow: 1, overflow: 'auto' }}>
        {loading ? (
          <Box sx={{ display: 'flex', justifyContent: 'center', p: 3 }}>
            <CircularProgress />
          </Box>
        ) : (
          <List>
            {conversations.map((conversation) => (
              <ListItem key={conversation.conversation_id} disablePadding>
                <ListItemButton
                  selected={selectedConversation?.conversation_id === conversation.conversation_id}
                  onClick={() => onConversationSelect(conversation)}
                >
                  <ListItemAvatar>
                    <Avatar>
                      <PersonIcon />
                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText
                    primary={conversation.other_username}
                    secondary={
                      <Box sx={{ display: 'flex', flexDirection: 'column' }}>
                        <Typography variant="body2" color="text.secondary" noWrap>
                          {getLastMessagePreview(conversation)}
                        </Typography>
                        <Typography variant="caption" color="text.secondary">
                          {formatLastMessageTime(conversation.last_message_time)}
                        </Typography>
                      </Box>
                    }
                  />
                </ListItemButton>
              </ListItem>
            ))}
            
            {conversations.length === 0 && !loading && (
              <Box sx={{ p: 3, textAlign: 'center' }}>
                <MessageIcon sx={{ fontSize: 48, color: 'text.secondary', mb: 2 }} />
                <Typography variant="body1" color="text.secondary">
                  No conversations yet
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Start a new conversation with someone!
                </Typography>
              </Box>
            )}
          </List>
        )}
      </Box>

      {/* Create Conversation Dialog */}
      <Dialog 
        open={createConversationDialog} 
        onClose={() => setCreateConversationDialog(false)}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>Start New Conversation</DialogTitle>
        <DialogContent>
          <Autocomplete
            options={searchResults}
            getOptionLabel={(option) => option.username}
            loading={searchLoading}
            value={selectedUser}
            onChange={(event, newValue) => setSelectedUser(newValue)}
            inputValue={searchQuery}
            onInputChange={(event, newInputValue) => {
              setSearchQuery(newInputValue);
              searchUsers(newInputValue);
            }}
            renderInput={(params) => (
              <TextField
                {...params}
                autoFocus
                margin="dense"
                label="Search users"
                fullWidth
                variant="outlined"
                placeholder="Type username to search..."
                InputProps={{
                  ...params.InputProps,
                  endAdornment: (
                    <>
                      {searchLoading ? <CircularProgress color="inherit" size={20} /> : null}
                      {params.InputProps.endAdornment}
                    </>
                  ),
                }}
              />
            )}
            renderOption={(props, option) => (
              <li {...props}>
                <Avatar sx={{ mr: 2 }}>
                  <PersonIcon />
                </Avatar>
                {option.username}
              </li>
            )}
            noOptionsText={
              searchQuery.length < 2 
                ? "Type at least 2 characters to search"
                : "No users found"
            }
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setCreateConversationDialog(false)}>Cancel</Button>
          <Button 
            onClick={createConversation} 
            variant="contained"
            disabled={!selectedUser}
          >
            Start Conversation
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default ConversationsList;
