import React, { useState, useEffect, useRef } from 'react';
import io from 'socket.io-client';
import {
  Box,
  Container,
  Paper,
  TextField,
  Button,
  Typography,
  List,
  ListItem,
  ListItemText,
  AppBar,
  Toolbar,
  Drawer,
  IconButton,
  Alert,
  Chip,
  Divider,
  Tabs,
  Tab,
  Badge
} from '@mui/material';
import {
  Send as SendIcon,
  Menu as MenuIcon,
  Lock as LockIcon,
  Logout as LogoutIcon,
  Group as GroupIcon,
  Message as MessageIcon,
  Tag as ChannelIcon,
  Settings as SettingsIcon
} from '@mui/icons-material';
import { roomsAPI, conversationsAPI } from '../utils/api';
import { encryptMessage, decryptMessage, clearEncryptionKey } from '../utils/encryption';
import SecureWebRTCService from '../services/SecureWebRTCService';
import VideoCallComponent from './VideoCallComponent';
import ServerList from './ServerList';
import ConversationsList from './ConversationsList';
import SettingsDialog from './SettingsDialog';

const ChatApp = ({ user, onLogout }) => {
  // Core state
  const [socket, setSocket] = useState(null);
  const [drawerOpen, setDrawerOpen] = useState(true);
  const [error, setError] = useState('');
  const [activeTab, setActiveTab] = useState(0); // 0: Servers, 1: Direct Messages, 2: Rooms (legacy)

  // Room/Channel state
  const [rooms, setRooms] = useState([]);
  const [currentRoom, setCurrentRoom] = useState(null);
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');

  // Server state
  const [selectedServer, setSelectedServer] = useState(null);
  const [selectedChannel, setSelectedChannel] = useState(null);

  // Private messaging state
  const [conversations, setConversations] = useState([]);
  const [selectedConversation, setSelectedConversation] = useState(null);
  const [privateMessages, setPrivateMessages] = useState([]);

  // Video call state
  const [webrtcService, setWebrtcService] = useState(null);
  const [isInVideoCall, setIsInVideoCall] = useState(false);

  // Settings state
  const [settingsOpen, setSettingsOpen] = useState(false);

  const messagesEndRef = useRef(null);

  // Initialize socket connection
  useEffect(() => {
    const token = localStorage.getItem('authToken');
    const newSocket = io('http://localhost:3001', {
      auth: { token }
    });

    newSocket.on('connect', () => {
      console.log('Connected to server');
      setSocket(newSocket);
      
      // Initialize Secure WebRTC service when socket connects
      const webrtc = new SecureWebRTCService(newSocket);
      setWebrtcService(webrtc);
    });

    newSocket.on('connect_error', (error) => {
      console.error('Connection error:', error);
      setError('Failed to connect to server');
    });

    // Room/Channel message handlers
    newSocket.on('new-message', (messageData) => {
      if (currentRoom && messageData.room_id === currentRoom.id) {
        const decryptedContent = decryptMessage(messageData.encrypted_content, messageData.room_id);
        setMessages(prev => [...prev, {
          ...messageData,
          decrypted_content: decryptedContent
        }]);
      }
    });

    // Private message handlers
    newSocket.on('new-private-message', (messageData) => {
      if (selectedConversation && 
          ((messageData.sender_id === selectedConversation.other_user_id && messageData.recipient_id === user.id) ||
           (messageData.sender_id === user.id && messageData.recipient_id === selectedConversation.other_user_id))) {
        const conversationKey = `dm_${Math.min(messageData.sender_id, messageData.recipient_id)}_${Math.max(messageData.sender_id, messageData.recipient_id)}`;
        const decryptedContent = decryptMessage(messageData.encrypted_content, conversationKey);
        setPrivateMessages(prev => [...prev, {
          ...messageData,
          decrypted_content: decryptedContent
        }]);
      }
    });

    newSocket.on('private-message-sent', (messageData) => {
      // Handle sent message confirmation
      if (selectedConversation && messageData.recipient_id === selectedConversation.other_user_id) {
        const conversationKey = `dm_${Math.min(user.id, selectedConversation.other_user_id)}_${Math.max(user.id, selectedConversation.other_user_id)}`;
        const decryptedContent = decryptMessage(messageData.encrypted_content, conversationKey);
        setPrivateMessages(prev => [...prev, {
          ...messageData,
          decrypted_content: decryptedContent
        }]);
      }
    });

    // Error handling
    newSocket.on('message-error', (errorData) => {
      console.error('Message error:', errorData);
      setError(errorData.error || 'Failed to send message');
    });

    return () => {
      newSocket.close();
    };
  }, [currentRoom, selectedConversation, user.id]);

  // Load legacy rooms on component mount
  useEffect(() => {
    loadRooms();
  }, []);

  // Auto-scroll to bottom of messages
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages, privateMessages]);

  const loadRooms = async () => {
    try {
      const response = await roomsAPI.getRooms();
      setRooms(response.data);
      if (response.data.length > 0 && !currentRoom && activeTab === 2) {
        selectRoom(response.data[0]);
      }
    } catch (error) {
      setError('Failed to load rooms');
    }
  };

  const selectRoom = async (room) => {
    if (currentRoom) {
      socket?.emit('leave-room', currentRoom.id);
    }
    
    setCurrentRoom(room);
    setMessages([]);
    setSelectedChannel(null);
    setSelectedConversation(null);
    setPrivateMessages([]);
    socket?.emit('join-room', room.id);

    try {
      const response = await roomsAPI.getMessages(room.id);
      const decryptedMessages = response.data.map(msg => ({
        ...msg,
        decrypted_content: decryptMessage(msg.encrypted_content, room.id)
      }));
      setMessages(decryptedMessages);
    } catch (error) {
      setError('Failed to load messages');
    }
  };

  const selectChannel = async (channel) => {
    if (currentRoom) {
      socket?.emit('leave-room', currentRoom.id);
    }
    
    setSelectedChannel(channel);
    setCurrentRoom(channel);
    setMessages([]);
    setSelectedConversation(null);
    setPrivateMessages([]);
    socket?.emit('join-room', channel.id);

    try {
      const response = await roomsAPI.getMessages(channel.id);
      const decryptedMessages = response.data.map(msg => ({
        ...msg,
        decrypted_content: decryptMessage(msg.encrypted_content, channel.id)
      }));
      setMessages(decryptedMessages);
    } catch (error) {
      setError('Failed to load messages');
    }
  };

  const selectConversation = async (conversation) => {
    if (currentRoom) {
      socket?.emit('leave-room', currentRoom.id);
    }
    
    setSelectedConversation(conversation);
    setCurrentRoom(null);
    setSelectedChannel(null);
    setMessages([]);
    setPrivateMessages([]);
    socket?.emit('join-conversation', conversation.conversation_id);

    try {
      const response = await conversationsAPI.getMessages(conversation.conversation_id);
      const conversationKey = `dm_${Math.min(user.id, conversation.other_user_id)}_${Math.max(user.id, conversation.other_user_id)}`;
      const decryptedMessages = response.data.map(msg => ({
        ...msg,
        decrypted_content: decryptMessage(msg.encrypted_content, conversationKey)
      }));
      setPrivateMessages(decryptedMessages);
    } catch (error) {
      setError('Failed to load messages');
    }
  };

  const sendMessage = () => {
    if (!newMessage.trim() || !socket) return;

    // Validate message length (4000 chars for original message, accounting for encryption overhead)
    if (newMessage.length > 4000) {
      setError('Message too long (maximum 4000 characters)');
      return;
    }

    try {
      if (selectedConversation) {
        // Send private message
        const conversationKey = `dm_${Math.min(user.id, selectedConversation.other_user_id)}_${Math.max(user.id, selectedConversation.other_user_id)}`;
        const encryptedContent = encryptMessage(newMessage, conversationKey);
        
        // Additional check for encrypted content length
        if (encryptedContent.length > 5000) {
          setError('Message too long after encryption');
          return;
        }
        
        socket.emit('send-private-message', {
          recipientId: selectedConversation.other_user_id,
          encryptedMessage: encryptedContent
        });
      } else if (currentRoom) {
        // Send room/channel message
        const encryptedContent = encryptMessage(newMessage, currentRoom.id);
        
        // Additional check for encrypted content length
        if (encryptedContent.length > 5000) {
          setError('Message too long after encryption');
          return;
        }
        
        socket.emit('send-message', {
          roomId: currentRoom.id,
          encryptedMessage: encryptedContent
        });
      }
      setNewMessage('');
    } catch (error) {
      console.error('Error sending message:', error);
      setError('Failed to encrypt and send message');
    }
  };

  const handleLogout = () => {
    clearEncryptionKey();
    onLogout();
  };

  const formatTimestamp = (timestamp) => {
    return new Date(timestamp).toLocaleTimeString();
  };

  const getCurrentMessages = () => {
    return selectedConversation ? privateMessages : messages;
  };

  const getCurrentTitle = () => {
    if (selectedConversation) {
      return `Direct Message with ${selectedConversation.other_username}`;
    }
    if (selectedChannel) {
      return `${selectedServer?.name} # ${selectedChannel.name}`;
    }
    if (currentRoom) {
      return currentRoom.name;
    }
    return 'ConvoFlow';
  };

  return (
    <Box sx={{ display: 'flex', height: '100vh' }}>
      <AppBar position="fixed" sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }}>
        <Toolbar>
          <IconButton
            color="inherit"
            edge="start"
            onClick={() => setDrawerOpen(!drawerOpen)}
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>
          <LockIcon sx={{ mr: 1 }} />
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            {getCurrentTitle()}
          </Typography>
          <Typography variant="body2" sx={{ mr: 2 }}>
            {user.username}
          </Typography>
          <IconButton color="inherit" onClick={() => setSettingsOpen(true)}>
            <SettingsIcon />
          </IconButton>
          <IconButton color="inherit" onClick={handleLogout}>
            <LogoutIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      <Drawer
        variant="persistent"
        open={drawerOpen}
        sx={{
          width: 320,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: 320,
            boxSizing: 'border-box',
            marginTop: '64px',
            height: 'calc(100vh - 64px)',
          },
        }}
      >
        <Box sx={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
          {/* Navigation Tabs */}
          <Tabs
            value={activeTab}
            onChange={(e, newValue) => setActiveTab(newValue)}
            variant="fullWidth"
            sx={{ borderBottom: 1, borderColor: 'divider' }}
          >
            <Tab icon={<GroupIcon />} label="Servers" />
            <Tab icon={<MessageIcon />} label="Direct" />
            <Tab icon={<ChannelIcon />} label="Rooms" />
          </Tabs>

          {/* Tab Content */}
          {activeTab === 0 && (
            <ServerList
              onServerSelect={setSelectedServer}
              onChannelSelect={selectChannel}
              selectedServer={selectedServer}
              selectedChannel={selectedChannel}
            />
          )}

          {activeTab === 1 && (
            <ConversationsList
              onConversationSelect={selectConversation}
              selectedConversation={selectedConversation}
            />
          )}

          {activeTab === 2 && (
            <Box sx={{ p: 2, flexGrow: 1, overflow: 'auto' }}>
              <Typography variant="h6" gutterBottom>
                Legacy Rooms
              </Typography>
              <List>
                {rooms.map((room) => (
                  <ListItem
                    key={room.id}
                    button
                    selected={currentRoom?.id === room.id && !selectedChannel && !selectedConversation}
                    onClick={() => selectRoom(room)}
                  >
                    <ListItemText primary={room.name} />
                  </ListItem>
                ))}
              </List>
            </Box>
          )}
        </Box>
      </Drawer>

      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 0,
          marginTop: '64px',
          marginLeft: drawerOpen ? '320px' : 0,
          transition: 'margin-left 0.3s',
          display: 'flex',
          flexDirection: 'column',
          height: 'calc(100vh - 64px)',
        }}
      >
        {/* Error Alert */}
        {error && (
          <Alert severity="error" onClose={() => setError('')} sx={{ m: 2 }}>
            {error}
          </Alert>
        )}

        {(currentRoom || selectedConversation) ? (
          <>
            {/* Messages Area */}
            <Box sx={{ flexGrow: 1, overflow: 'auto', p: 2 }}>
              <Container maxWidth="md">
                <Paper elevation={1} sx={{ p: 2, mb: 2, bgcolor: 'info.light' }}>
                  <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center' }}>
                    <LockIcon sx={{ mr: 1, fontSize: 16 }} />
                    Messages are end-to-end encrypted
                  </Typography>
                </Paper>
                
                <List sx={{ bgcolor: 'background.paper', borderRadius: 1 }}>
                  {getCurrentMessages().map((message, index) => (
                    <React.Fragment key={message.id || index}>
                      <ListItem alignItems="flex-start">
                        <ListItemText
                          primary={
                            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                              <Typography
                                component="span"
                                variant="subtitle2"
                                color="primary"
                              >
                                {message.username}
                              </Typography>
                              <Typography variant="caption" color="text.secondary">
                                {formatTimestamp(message.timestamp)}
                              </Typography>
                            </Box>
                          }
                          secondary={
                            <Typography
                              component="span"
                              variant="body1"
                              color="text.primary"
                              sx={{ mt: 1, display: 'block' }}
                            >
                              {message.decrypted_content}
                            </Typography>
                          }
                        />
                      </ListItem>
                      {index < getCurrentMessages().length - 1 && <Divider variant="inset" />}
                    </React.Fragment>
                  ))}
                  <div ref={messagesEndRef} />
                </List>
              </Container>
            </Box>

            {/* Video Call Component */}
            {webrtcService && currentRoom && (
              <VideoCallComponent
                webrtcService={webrtcService}
                roomId={currentRoom.id}
                isInCall={isInVideoCall}
                onCallStateChange={setIsInVideoCall}
              />
            )}

            {/* Message Input */}
            <Paper sx={{ p: 2, m: 2, mt: 0 }}>
              <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
                <Box sx={{ display: 'flex', gap: 1 }}>
                  <TextField
                    fullWidth
                    variant="outlined"
                    placeholder={
                      selectedConversation 
                        ? `Message ${selectedConversation.other_username}...`
                        : `Message #${currentRoom?.name}...`
                    }
                    value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)}
                    onKeyPress={(e) => {
                      if (e.key === 'Enter' && !e.shiftKey) {
                        e.preventDefault();
                        sendMessage();
                      }
                    }}
                    multiline
                    maxRows={4}
                    error={newMessage.length > 4000}
                  />
                  <Button
                    variant="contained"
                    onClick={sendMessage}
                    disabled={!newMessage.trim() || newMessage.length > 4000}
                    sx={{ minWidth: 'auto', px: 2 }}
                  >
                    <SendIcon />
                  </Button>
                </Box>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                  <Typography 
                    variant="caption" 
                    color={newMessage.length > 4000 ? 'error' : 'text.secondary'}
                  >
                    {newMessage.length}/4000 characters
                  </Typography>
                  {newMessage.length > 4000 && (
                    <Typography variant="caption" color="error">
                      Message too long
                    </Typography>
                  )}
                </Box>
              </Box>
            </Paper>
          </>
        ) : (
          <Box
            sx={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              height: '100%',
              flexDirection: 'column',
              color: 'text.secondary',
            }}
          >
            <LockIcon sx={{ fontSize: 64, mb: 2 }} />
            <Typography variant="h5" gutterBottom>
              Welcome to ConvoFlow
            </Typography>
            <Typography variant="body1" textAlign="center">
              Select a server channel, direct message, or legacy room to start chatting
            </Typography>
          </Box>
        )}
      </Box>
      
      <SettingsDialog
        open={settingsOpen}
        onClose={() => setSettingsOpen(false)}
      />
    </Box>
  );
};

export default ChatApp;
